import React, { useState } from 'react'

import Button from '../../components/Button'
import Card from '../../components/Card'
import ConnectWallet from '../../components/ConnectWallet'
import Section from '../../components/Section'
import Socials from '../../components/Socials'
import useActiveWeb3React from '../../hooks/useActiveWeb3React'
import { useBnbHive } from '../../providers/BNBHiveProvider/BNBHiveContext'
import { roundTo } from '../../utils'

import './ReHireSection.css'

const ReHireSection = () => {
  const { account: accountAddress } = useActiveWeb3React()
  const { account, contract } = useBnbHive()
  const [loading, setLoading] = useState<boolean>(false)
  const rewards = roundTo(account.rewards || '0', 4)
  const honey = roundTo(account.honey, 3);
  const canClaim = rewards > 0

  const onReHire = async () => {
    if (accountAddress && contract) {
      setLoading(true)
      try {
        await contract.reinvest(accountAddress)
        setLoading(false)
      } catch (e) {
        alert((e as any).message)
        setLoading(false)
      }
    }
  }

  const onClaim = async () => {
    if (accountAddress && contract) {
      setLoading(true)
      try {
        await contract.withdraw(accountAddress)
        setLoading(false)
      } catch (e) {
        alert((e as any).message)
        setLoading(false)
      }
    }
  }

  return (
    <Section className="re-hire-section" small>
      <Card title="Your honey">
        <div className="premium-amount">
          {honey} HONEY / {rewards} BNB
        </div>
        <div className="re-hire__buttons">
          {!accountAddress ? (
            <ConnectWallet />
          ) : (
            <>
              <Button
                disabled={!canClaim}
                type="primary"
                onClick={onReHire}
                loading={loading}
              >
                Hire more bees
              </Button>
              <Button
                disabled={!canClaim}
                type="secondary"
                onClick={onClaim}
                loading={loading}
              >
                Claim BNB
              </Button>
            </>
          )}
        </div>
        <div>
          <div className="card-title">Socials</div>
          <Socials />
        </div>
      </Card>
    </Section>
  )
}

export default ReHireSection
